.code-editor {
  width: 100% !important;
  height: 69vh !important;
}

.buttons {
  display: flex; 
  align-items: center;
  width: 100%;
  height: 4vh;
  padding: 0px;
}

#button {
  color: white;
  background-color: #324a50; /* Green */
  border: 2px solid #9aacb1;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  flex-grow: 1;
  cursor: pointer;
  transition-duration: 0.4s;
}

#button:hover {
  background-color: #45a049;
}