#output {
  display: flex;
  width: 100%;
  height: 100%;
  box-sizing: border-box; /* Include padding and border in element's total width and height */
  font-size: 20px;
  color: green;
  background-color: #000000;
}
.output-div {
  width: 75vw;
  height: 100%;
  overflow: auto;
  padding: 10px;
  background-color: #000000;
  color: #ffffff;
  font-family: monospace;
}